import base from '@/services/servicio-base'

const servicio = {}

servicio.tiposEmpleados = () => {
    return base.get(`/comisiones/tiposEmpleado`,{})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.rptComisiones = (fechaInicial, fechaFinal, tipoEmpleado) => {
    return base.post(`/comisiones/rpt-comisiones`,{fechaInicial, fechaFinal, tipoEmpleado})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.comisiones = (codTipoEmpleado) => {
    return base.post(`/comisiones/obtenerComisiones`,{codTipoEmpleado})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.empleados = () => {

    return base.get(`/comisiones/obtenerEmpleadosComisiones`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.comisionesPorEmpleado = (codEmpleado, diaComision) => {

    return base.post(`/comisiones/obtenerComisionesPorEmpleado`,{codEmpleado, diaComision})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarComisiones = (data) => {
    return base.post(`/comisiones/guardar-comisiones`,{data})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.asignarComisiones = (data) => {
    return base.post(`/comisiones/asignar-comisiones`,{data})
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}
export default servicio